@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}
body {
  margin: 0;
  background-color: #3f3f3f;
}


.new-exp__item {
  display: inline-block;
  margin: 1em;
  box-sizing: content-box;
}

.new-exp__button button {
  color: #2a2a2a;
  border: 3px solid #a36969;
  font-size: 1rem;
  font-weight: bold;
  padding: .5rem;
  border-radius: 1em;
  float: right;
}

.new-exp__item label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.new-exp__item input {
  padding: 0.5em;
  margin: .5rem;
  border-radius: .5rem;
  border: 3px solid #a36969;
  font: inherit;
  width: 20rem;
  max-width: 100%;
}

.new-exp{
  color: dimgray;
  font-size: 20px;
  border: 2px solid #2b0808;
  height: auto;
  margin: 1rem 1rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  padding: 0.5rem;
  border-radius: 12px;
  background-color: wheat;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.title{
  text-align: center;
  transition: word-spacing;
  -webkit-transform: translateY(3);
          transform: translateY(3);
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: wheat;
}


.exp-filter {
  color: white;
  padding: 0 1rem;
}

.exp-filter__control{
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0;
}

.exp-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.exp-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
}

.item {
  font-size: 20px;
  border: 2px solid #2b0808;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  border-radius: 12px;
  background-color: #4b4b4b;
  color: wheat;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem;
}

.item__dec {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1 1;
}

.item__price {
  background-color: #282c34;
  color: wheat;
  border: 2px solid #a36969;
  font-size: 1rem;
  font-weight: bold;
  padding: 0.5rem;
  border-radius: 12px;
}

.item__title {
  font-size: 1.5rem;
  flex: 1 1;
  margin: 0 1rem;
  color: bisque;

}
@media (min-width: 580px) {
  .item__dec {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1 1;
  }

  .item__dec h2 {
    font-size: 1.25rem;
  }

  .item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}

.expense-date {
  display: flex;
  flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
  border: 1px solid wheat;
  background-color: #2a2a2a;
  color: wheat;
  border-radius: 12px;
  align-items: center;
  justify-content: center;
}

.expense-date__month {
  font-size: 0.75rem;
  font-weight: bold;
}

.expense-date__year {
  font-size: 0.75rem;
}

.expense-date__day {
  font-size: 1.5rem;
  font-weight: bold;
}

